// Gameboard styling with variables
.tic-tac-toe-container {
  text-align: center;

  .gameboard {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 5px;
    width: 500px;
    height: 500px;
  }
}

// Cell styling with variables
.cell {
  background-color: $gunmetal;
  border: 1px solid $silver;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 75px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5), 0 0 15px rgba(0, 0, 0, 0.8);
  transition: 0.4s ease-in-out;
}
.cell:empty:hover {
  transition: transform 0.4s ease-in-out;
  transform: scale(1.1);
}

/* Assigning colors for X and O */
.x-mark {
  color: #b30000;
  text-shadow: 1px 1px 2px #8c001a;
}

.o-mark {
  color: #0066cc;
  text-shadow: 1px 1px 2px #00457a;
}

.x-mark,
.o-mark {
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
  background-color: #808080;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5), 0 0 15px rgba(0, 0, 0, 0.8);
  pointer-events: none;
  transform: scale(0.95);
}

// Winner banner styling
/* Styling for overlay */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Styling for winner banner container */
.winner-banner-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 200px;
  height: 200px;
  text-align: center;
  background-color: #b0c4de;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5), 0 0 15px rgba(0, 0, 0, 0.8);
}

/* Styling for restart button */
.restart {
  display: block;
  margin: 10px;
  margin-top: 30px;
  width: 100px;
  height: 40px;
  background-color: #b0c4de;
  color: #333;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.5), 0 0 15px rgba(0, 0, 0, 0.8);
  &:hover {
    background-color: $gunmetal;
    color: $silver;
  }
}

// handle animation on win
.animate-score {
  background-image: linear-gradient(to right, #ff9999, #99ccff);
  background-size: 200% 100%;
  animation: scoreboardAndFlash 1s linear forwards infinite alternate;
}

@keyframes scoreboardAndFlash {
  0% {
    background-position: 0% 50%;
    border-color: transparent;
  }
  100% {
    background-position: 100% 50%;
    border-color: black;
  }
}

@media (max-width: 750px) {
  .tic-tac-toe-container .gameboard {
    width: 400px;
    height: 400px;
  }
}
@media (max-width: 450px) {
  .tic-tac-toe-container .gameboard {
    width: 350px;
    height: 350px;
  }
}
